.container {
  justify-content: center;
  align-items: flex-start;
  padding-top: 5vh;
  margin: 0 auto;
  overflow: auto;
  text-align: center;
  min-height: 90vh;
  background: linear-gradient(to bottom, burlywood, #d35400);
  font-family: Arial, sans-serif;
  min-height: 86.5vh;
}

.form {
  width: 40%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1em;
  background-color: rgb(49, 113, 170);
  padding: 1em;
  border-radius: 1em;
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.1),
    0px 4px 6px -2px rgba(0, 0, 0, 0.05);
  outline: black solid 1px;
}

.form input[type="text"] {
  width: 100%;
  padding: 0.5rem;
  margin: 1rem 0;
}

.note {
  font-size: 12px;
  margin: 1rem 0;
}
