body,
html {
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  overflow: auto;
  text-align: center;
  min-height: 86.5vh;
  padding-top: 5vh;
  background: linear-gradient(to bottom, burlywood, #d35400);
  font-family: Arial, sans-serif;
}

.contentBox {
  width: 40%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1em;
  background-color: rgb(49, 113, 170);
  padding: 1em;
  border-radius: 1em;
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.1),
    0px 4px 6px -2px rgba(0, 0, 0, 0.05);
  outline: black solid 1px;
}
