.messageBox {
  width: 60%;
  margin: 20px auto;
  padding: 1em;
  background-color: rgb(49, 113, 170);
  border-radius: 1em;
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.1),
    0px 4px 6px -2px rgba(0, 0, 0, 0.05);
  color: white;
}

button {
  margin-top: 2em;
  padding: 1em;
  font-size: 1em;
  cursor: pointer;
}
